.image{
    border-radius: 20%; 
    width:25rem;
    height: 25rem;
    margin:1rem;
    float:left;
    border: 4px #f8b500 solid;
}
.text{
    margin: 1rem;
    padding: 1rem;
    float:left;
    width: 28rem;
    text-align: left;
    border: 3px dashed #393e46;
}
.col{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
.changepage {
    position: relative;
    margin-top:10rem;
}
.changepage a{
    position: absolute;
    bottom: 1rem;
    border: 3px solid #5c636e;
    -ms-flex-line-pack: center;
        align-content: center;
    background-color: #5c636e;
    padding: 1rem;
    margin:1rem;
    border-radius: 20%; 
    font-size:20px;
    color:#f7f7f7;
}
.changepage a:hover{
    color: #f8b500;
    text-decoration: none;
}
.changepage a:focus{
    text-decoration: none;
}
.changepage div:nth-child(1) a {
    left: 0;
}
.changepage div:nth-child(2) a {
    right: 0;
}


