.backgroundcol{
    height: 60rem;
    margin: 1rem;
    text-align: center;
}
.background{
    background-image: url("./img/snowman.jpg");
    background-repeat: no-repeat; 
    background-size: cover;
    border: 5px #5c636e solid;
    margin-bottom: 5rem;
    margin-top: 3rem;
    height: 50rem;   
    width: 120rem;
    -webkit-transform: translate(-10%, 0);
        -ms-transform: translate(-10%, 0);
            transform: translate(-10%, 0);
}
.navbar{
    font-size: 120%;
    margin-bottom: 0rem;
    background-color: transparent;
    border-color: transparent;
}
.navbar-brand{
    font-size:2.5rem;
}
.navbar a{
    font-size:2.5rem !important;
}
.navbar li a{
    background-color: transparent !important;
    color: #ffffff !important;
}
.nav li a:focus{
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
}
.nav li a:hover{
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
}
.navbar li.focus a{
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
}
.navbar li.active a{
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
}
.texts{
    margin: 2rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    text-align: center;
    color:#fafafa;
    -ms-flex-align: center;
        align-items: center;
}
.texts p{
    font-size:2.5rem;
}
.navbar-brand{
    color:#ffffff !important;
}
ul.dropdown-menu {
    background-color: #f7f7f7;
    padding:0;
    width:25.5rem;
    border:none;
    text-align: center;
}
ul.dropdown-menu li a {
    color:#393e46 !important;
    font-size:2rem !important;
}
ul.dropdown-menu li{
    padding: 1.5rem;
    font-size:1.7rem;
}
li.dropdown open:active{
    color: #393e46; 
}




