.pic img{
    width:100%;
    height:100%;
    border: 3px solid #000000;
    cursor: pointer;
}
.pic img:hover{
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}
#gallery{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.pic{
    width: 40rem;
    height: 30rem;
    margin: 3rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}
.pic.open{
    position: fixed;
	top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width:80rem;
    height:60rem;
    z-index: 3;
}
#overlay{
        position: fixed;
        display: block;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.7);
        z-index: 2;
        cursor: pointer;
}


