.section{
    height: 45rem;
    margin: 1rem;
    width: 35rem;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border: 5px #f8b500 solid;
    text-align: center; 
    display: -ms-flexbox; 
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-line-pack: center;
        align-content: center;
    -ms-flex-pack: center;
        justify-content: center;   
}
#section1{
    background-image: url("./img/shoes.png");
}
#section2{
    background-image: url("./img/street.JPG");
}
#section3{
    background-image: url("./img/louise.png");
}
#section4{
    background-image: url("./img/castle.png");
}

.content{
    height: 8rem;
    width: 100%;
    background: rgba(0%,0%,0%,0.7);
}
.link:link {
    text-decoration: none; }
.link {
    color: #f7f7f7;
}
.link:hover .content{
    cursor: pointer;
    background: rgba(0%,0%,0%,0.3);
    color: #f7f7f7;
} 

  


